////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// layout mixins - start

@mixin edgtfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin edgtfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin edgtfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

// layout mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// transition mixins - start

@mixin edgtfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin edgtfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin edgtfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin edgtfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin edgtfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

// transition mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// checkbox mixins - start

$checkbox-size: 15px;
$checkbox-border-width: 1px;

%checkbox-style {
    position: relative;
    margin: 8px 0;
    line-height: 1;

    input[type=checkbox] {
        width: $checkbox-size;
        height: $checkbox-size;
        max-height: $checkbox-size;
        position: relative;
        display: inline-block;
        vertical-align: top;
        top: 0;
        left: 0;
        margin: 0;
    }

    input[type=checkbox] + label {
        display: inline-block;
        pointer-events: none;
        cursor: pointer;;
        position: absolute;
        top: 0;
        left: 0;
        line-height: 0;
    }

    input[type=checkbox] + label span.edgtf-label-text {
        padding-left: 10px;
        display: inline-block;
        line-height: $checkbox-size;
        color: $default-heading-color;
    }

    input[type=checkbox] + label .edgtf-label-view {
        display: inline-block;
        width: $checkbox-size;
        height: $checkbox-size;
        box-sizing: border-box;
        vertical-align: top;
        cursor: pointer;
        border: $checkbox-border-width solid $default-border-color;
        background-color: $default-background-color;
        border-radius: 2px;

        &:hover {
            cursor: pointer;
        }
    }

    input[type=checkbox] + label .edgtf-label-view:after {
        content: '';
        width: $checkbox-size / 2 - $checkbox-border-width;
        height: $checkbox-size / 2 - $checkbox-border-width;
        position: absolute;
        top: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        left: ($checkbox-size - ($checkbox-size / 2 - $checkbox-border-width)) / 2;
        background-color: $first-main-color-black;
        opacity: 0;

        @include edgtfTransition(opacity 0.3s ease-in-out);
    }

    input[type=checkbox]:checked + label .edgtf-label-view:after {
        opacity: 1;
    }
}

// checkbox mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// common mixins - start

@mixin edgtfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin edgtfImageOverlayHoverStyle($with-hover: true) {
    @if ($with-hover) {
        &:hover {
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include edgtfAbsoluteHolderLayout();

            content: '';
            background-color: rgba($default-heading-color, .4);
            opacity: 0;

            @include edgtfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include edgtfAbsoluteHolderLayout();

        content: '';
        background-color: rgba($default-heading-color, .4);
        opacity: 0;

        @include edgtfTransition(opacity .2s ease-in-out);
    }
}

@mixin edgtfStandardPaginationStyle($list_type: null) {
    @include edgtfRelativeHolderLayout(top);

    margin: 20px 0 0;
    clear: both;

    ul {
        @include edgtfRelativeHolderLayout(top);

        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;
            margin: 0 3px;
			
	        .page-numbers{
		        font-size: 19px;
		        &.current{
			        color: $first-main-color;
		        }
          
	        }
         
	        a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                margin: 0;
                padding: 0;
                
                &.prev, &.next {
                    color: $first-main-color;
                }
        
                &.prev{
                    padding-right: 16px;
                }
        
                &.next{
                    padding-left: 16px;
                }
            }

            &.edgtf-pag-active {
                a {
                    color: $first-main-color;
                }
            }

            &.edgtf-pag-prev,
            &.edgtf-pag-next,
            &.edgtf-pag-first,
            &.edgtf-pag-last {
                margin: 0 2px;

                a {
                    font-size: 24px;

                    span {
                        display: block;
                        line-height: inherit;

                        &:before {
                            display: block;
                            line-height: inherit;
                        }
                    }
                }
            }

            @if ($list_type == 'shortcode') {
                &.edgtf-pag-prev {
                    a {
                        opacity: 0;
                    }
                }

                &.edgtf-pag-next {
                    a {
                        opacity: 1;
                    }
                }

            } @else if ($list_type == 'shop') {
                span {
                    position: relative;
                    display: inline-block;
                    vertical-align: top;
                    margin: 0;
                    padding: 0;
                    color: $first-main-color-black;
                }

                a {
                    &.next,
                    &.prev {
                        font-size: 0;
                        line-height: 0;

                        &:before {
                            display: block;
                            font-family: 'ElegantIcons'; // same icon pack as in our templates for pagination
                            font-size: 18px;
                            line-height: 26px;
                            -webkit-font-smoothing: antialiased;
                            -moz-osx-font-smoothing: grayscale;
                        }
                    }

                    &.prev {
                        margin-top: 2px;
                        margin-right: -3px;

                        &:before {
                            content: "\34";
                        }
                    }

                    &.next {
                        margin-top: 2px;
                        margin-left: -3px;

                        &:before {
                            content: "\35";
                        }
                    }
                }
            }
        }
    }
}

@mixin edgtfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: $default-text-font;
    font-size: 13px;
    line-height: 2em;
    letter-spacing: 0.2em;
    font-weight: 600;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;

    @include edgtfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out);
}

@mixin edgtfButtonSize($size: medium) {
    @if ($size == 'small') {
        padding: 11px 36px;

    } @else if ($size == 'medium') {
        padding: 12px 36px 12px 37px;

    } @else if ($size == 'large') {
        padding: 13px 66px;

    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 13px 26px;
    }
}

@mixin edgtfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin edgtfButtonSolidColor($important: null) {
    color: #fff $important;
    background-color: $default-button-color $important;
    border: 1px solid transparent $important;
}

@mixin edgtfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color-black $important;
}

@mixin edgtfButtonOutlineColor($important: null) {
    color: $default-button-color $important;
    background-color: transparent $important;
    border: 1px solid $default-button-color $important;
}

@mixin edgtfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color-black $important;
    border-color: $first-main-color-black $important;
}

@mixin edgtfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// mixins styles - start

%input-style {
    background-color: transparent;
    border: 1px solid $default-border-color;
    border-radius: 0;
    box-sizing: border-box;
    color: $default-text-color;
    cursor: text;
    font-family: inherit;
    font-size: 15px;
    font-weight: inherit;
    line-height: calc(#{$input-height} - (#{$input-vertical-padding} * 2) - 2px);
    margin: 0 0 $input-margin;
    outline: 0;
    padding: $input-vertical-padding $input-horizontal-padding;
    position: relative;
    width: 100%;

    -webkit-appearance: none;

    @include edgtfTransition(border-color 0.2s ease-in-out);
    
    @include edgtfPlaceholder {
        color: inherit;
    }
}

// mixins styles - end

